@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-height: 20px;
    margin: 0 auto;
    background-color: black;
  }

  .navbar .login {
    margin-right: 0px;
    
    font-size: large;
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 7px;
    border-radius: 7px;
    cursor: pointer;
}

.navbar .status {
  position: absolute;
  right: 200px;
  font-size: large;
  color: #fff;
}

.navbar button {
    font-size: large;
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
  }

  .navbar a {
    color: white;
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
  }
  .navbar a:hover {
    color: #f1356d;
  }

  .navbar img{
      height: 50px;
  }
.App {
    margin-left: 0px;
  }
  
  input {
    display: block;
    margin: 0px 0 0px;
  }
.background img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
}
.matches h1 {
    margin: auto;
    text-align: center;
    padding: 10px;
    border-radius: 16px;
    width: 1000px;
    border: 0;
    background: tomato;
}

.matches .list {
    margin-top: 50px;
    margin: auto;
    text-align: center;
}

.menu {
    margin-top: 20px;
    margin-left: 30px;
}

.menu a {
    margin: auto;
    text-decoration: none;
    font-size: large;
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
  }

.all {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: 1fr;
}

.menu {
    grid-column: 1;
    grid-row: 1;
} 

.matches {
    grid-column: 2;
    grid-row: 1;
}

