.match-preview {
    padding: 10px 16px;
    margin: auto;
    width: 900px;
    border-bottom: 1px solid white;
}

.match-preview:hover {
    box-shadow: 1px 3px 5px black;
}

.match-preview h2 {
    font-size: 20px;
    color: red;
    margin-bottom: 8px;
}

.match-preview a {
    text-decoration: none;
}

.column {
    float: left;
    width: 33.3%;
}

.match-preview:after {
    content: "";
    display: table;
    clear: both;
  }
.pitch-preview {
    border-bottom: 1px solid black;
    margin: auto;
    width: 900px;
    height: 220px;
}

.pitch-preview img {
    width: 40%;
    height: 80%;
    margin-left: 10px;
}

.pitches-list .info {
    margin-top: 60px;
}
.create {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
  }
  .create label {
    text-align: left;
    display: block;
  }
  .create h2 {
    margin: auto;
    text-align: center;
    padding: 10px;
    border-radius: 16px;
    width: 300px;
    border: 0;
    background: tomato;
  }
  .create input, .create select, .create textarea{
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
  }
  .create .add {
    margin-top: 50px;
    width: 100%;
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
  }


  
/* Popup style */
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
 
.box {
  position: relative;
  width: 70%;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  overflow: auto;
}
 
.close-icon {
  content: 'x';
  cursor: pointer;
  position: fixed;
  right: calc(15% - 30px);
  top: calc(100vh - 85vh - 33px);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}
